import Vue from 'vue';
import VueRouter from 'vue-router';
import books from '../views/books.vue';
import home from '../views/home.vue';
import shabab from '../views/shabab.vue';
import contact from '../views/contact.vue';
import AboutUs from '../components/global/aboutUs.vue';
import ourHistory from '@/components/global/ourHistory.vue';
import newNews from '@/components/global/newNews.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home-vue',
    component: home,
  },
  {
    path: '/about-us',
    name: 'about-us',
    component: AboutUs,
  },
  {
    path: '/history',                   
    component: ourHistory,
  },
  {
    path: '/newNews',
    component: newNews,
  },
  {
    path: '/contactPage',
    name: 'contact-page',
    component: contact
  },
  {
    path: '/shababPage',
    name: 'shabab-page',
    component: shabab
  },
  {
    path: '/bookVue',
    name: 'book-vue',
    component: books
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.afterEach((to, from, next) => {
  if (to.hash) {
    setTimeout(() => {
      const el = document.getElementById(to.hash.slice(1));
      if (el) {
        el.scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" });
      }
    }, 200);
  } else {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }
});

