<template>
    <div class="container section" id="history">
        <div class="row ourHistory d-flex justify-content-center " :style="{ 'backgroundImage': `url(${backImage})` }">
            <div class="funfact col-md-3 col-sm-6 " v-for="(work, index) in works" :key="index">
                <div class="text-center pt-5 pb-3 ">
                    <font-awesome-icon :icon="work.icon" class="fs-2" />
                    <h2>+{{ work.number }}</h2>
                    <p class="pb-4">{{ work.title }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'ourHistory',
    data: () => {
        return {
            intervalIds: [],
            targetElement: false,
            maxNum: 0,
            backImage: require('../../assets/img/historyBack.jpeg'),
            zero: 0,
            works: [{ title: 'كتب مقروءة', number: 0, targetNumber: 450, icon: 'fa-solid fa-book-open', }, {
                title: 'كتب مصورة', number: 0, targetNumber: 150, icon: 'fa-solid fa-print'
            },
            {
                title: 'أفلام وثائقية', number: 0, targetNumber: 75, icon: 'fa-solid fa-film'
            },
            {
                title: 'أنشطة طلابية متنوعة', number: 0, targetNumber: 100, icon: 'fa-solid fa-user-graduate'
            }]
        };
    },
    mounted() {
        window.addEventListener('scroll', this.handleScrollY);
    },
    methods: {
        handleScrollY() {
            if (window.scrollY > 600) {
                this.works.forEach(work => {
                    let intervalId = setInterval(() => {
                        if (work.number < work.targetNumber) {
                            work.number++;
                        } else {
                            clearInterval(intervalId);
                        }
                    }, 400); // كل ثانية واحدة (يمكن تغيير الفاصل الزمني حسب الحاجة)
                    this.intervalIds.push(intervalId); // حفظ معرف التكرار لكل شخص
                });
            }
        },
    },
    beforeDestroy() {
        this.intervalIds.forEach(intervalId => {
            clearInterval(intervalId); // تأكد من إيقاف التكرار قبل تدمير المكون
        });
    }
}
</script>

<style scoped lang="scss">
.ourHistory {
    position: relative;
    background-position: 50% 88px;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: repeat-y;
    box-sizing: border-box;
    display: block;
    overflow: hidden;

    .funfact {
        color: aliceblue;
        z-index: 1;
        position: relative;
    
        h2 {
            line-height: 3;
            font-family: 'Marhey';
        }
    }

    .funfact::before {
        content: '';
        position: absolute;
        width: 1px;
        height: 100%;
        color: #fff;
        background-color: #fff;
        right: 1%;
        top: 0;
        transition: all 1s;
        transform: translateY(+100%)
    }
    
    .funfact::after {
        content: '';
        position: absolute;
        width: 1px;
        height: 100%;
        color: #fff;
        background-color: #fff;
        right: 99%;
        top: 0;
        transition: all 1s;
        transform: translateY(-100%)
    }
    
    .funfact:hover::before,
    .funfact:hover::after {
        transform: translateY(0%)
    }
    
    .btn-success {
        z-index: 1;
        --bs-btn-color: #fff;
        --bs-btn-bg: #00a19b;
        --bs-btn-border-color: #ffffff;
        --bs-btn-hover-color: #00a19b;
        --bs-btn-hover-bg: #ffffff;
        --bs-btn-hover-border-color: #00a19b;
        --bs-btn-focus-shadow-rgb: 60, 153, 110;
    }
}

.ourHistory::before {
    background-color: rgba(9, 152, 160, 0.9) !important;
    background: rgba(17, 17, 17, 0.5) none repeat scroll 0 0;
    content: " ";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 0;
}

@media (max-width: 991.98px) {
    .funfact {
        padding-left: 0 !important;
    }
    .funfact div {
        padding-bottom: 0 !important;
        h2 {
            margin-bottom: 0 !important;
            line-height: 2 !important;
        }
        p {
            font-size: 16px !important;
        }
    }
}

@media (max-width: 767.98px) {

    .funfact::after {
        transform: translateY(-200%)
    }

    .funfact::before {
        transform: translateY(200%)
    }

}

@media (max-width: 575.98px) {
    .funfact::after {
        transform: translateY(-400%)
    }

    .funfact::before {
        transform: translateY(400%)
    }

    .funfact div {
        padding-left: calc(var(--bs-gutter-x)* 0.7);
    }
}
</style>
