<template>
    <div class="about section mt-0 mt-sm-4 " id="about-us">
        <div class="container">
            <div class="row d-flex justify-content-center align-items-center">
                <div class="col-6 col-sm-4 ">
                    <img v-show="isActive" :src="backGrouund" class="img-fluid  pb-sm-4 p-xl-0" alt="">
                </div>
                <div class="col-12 col-sm-8 text-end " dir="rtl">
                    <!-- about-res576 كلاس لضبط القسم في الشاشة قياس 576  -->
                    <div class="about-res576">
                        <h3 ref="titleAbout" class="fw-bold pe-1 mb-3 d-none d-sm-block"></h3>
                        <p ref="contentAbout" class="fw-bold ps-2" data-autotyper-interval="[25,50]"
                            data-autotyper-auto-start="1000"></p>
                        <button type="button" class="btn btn-success  mt-1 mb-3 pt-0 pb-1 pt-lg-1" dir="rtl"> <a
                                class=""
                                href="https://drive.google.com/file/d/12ga5hEcrO1RGV0dR1i9B_-XcqZiJidpo/view?usp=sharing">اعرف
                                المزيد
                                ..</a></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import autotyper from 'autotyper';

const typer = Object.create(autotyper);
const contentAbout = ref(null);
const titleAbout = ref(null);
const isActive = ref(false);

const backGrouund = ref(require('../../assets/img/logoTitle.png'));


onMounted(() => {
    typer.init(contentAbout.value, {
        text: 'مركز تعليمي ثقافي يحاول محاكاة المراكز الإسلامية في أوروبا قدر المستطاع، يعمل برؤية إسلامية معتدلة تجمع بين التأصيل العلمي والنشاط الحركي..|',
    });
    titleAbout.value.innerText = 'من نحن؟';
});

onMounted(() => {
    window.addEventListener('scroll', handleScrollY);
});


//   isActive إظهار الصورة اعتمادا على السيكرول وقيمة
const handleScrollY = () => {
    if (window.scrollY > 100) {
        isActive.value = true; // Update isActive to true when scrolled more than 100px
    } else {
        isActive.value = false; // Update isActive to false when scrolled less than or equal to 100px
    }
};

</script>

