<template>
    <nav class="p-0 navbar navbar-expand-lg fixed-top">
        <div class="container p-2" :class="{ 'onScroll': !topOfPage }">
            <router-link class="navbar-brand me-0 p-0" to="/"><img class="ps-2 p-1" :src="logo" alt=""></router-link>
            <button @click.prevent="toggleMenu" :class="[activeChange != false ? 'X' : '']"
                class="navbar-toggler icon border-0 pt-1 d-lg-none d-flex flex-column" dir="rtl" type="button"
                data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup"
                aria-expanded="false" aria-label="Toggle navigation">
                <span class="bar1"></span>
                <span class="bar2"></span>
                <span class="bar3"></span>
            </button>
            <div class="collapse navbar-collapse" :class="[activeChange === false ? '' : 'respon']"
                id="navbarNavAltMarkup" dir="rtl">
                <div class="navbar-nav">
                    <router-link v-for="link in links" :key="link.id" :to="link.to" class="nav-link"
                        :aria-current="link.ariaCurrent"><font-awesome-icon :icon="link.icon" />
                        {{ link.text }}</router-link>
                </div>
            </div>
        </div>
    </nav>

</template>
<script>
import '@coreui/coreui/dist/css/coreui.min.css'
export default {
    name: 'headerApp',
    data: function () {
        return {
            logo: require('../assets/img/logo.png'),
            activeChange: false,
            topOfPage: true,
            links: [
                { id: 1, to: '/', text: 'الرئيسية', ariaCurrent: 'page', icon: 'fa-solid fa-house' },
                { id: 2, to: '/#about-us', text: 'من نحن؟', ariaCurrent: null, icon: 'fa-solid fa-address-card' },
                { id: 3, to: '/#history', text: 'الإنجازات', ariaCurrent: null, icon: 'fa-solid fa-list-check' },
                { id: 4, to: '/#newNews', text: 'اخر الأخبار', ariaCurrent: null, icon: 'fa-solid fa-calendar-week' },
                { id: 5, to: '/bookVue', text: 'الكتب', ariaCurrent: null, icon: 'fa-solid fa-book' },
                { id: 6, to: '/shababPage', text: 'شبابنا', ariaCurrent: null, icon: 'fa-solid fa-people-group' },
                { id: 7, to: '/contactPage', text: 'تواصل معنا', ariaCurrent: null, icon: 'fa-solid fa-address-card' }
            ]
        }
    },
    beforeMount() {
        window.addEventListener('scroll', this.handleScroll)

    },
    mounted() {
        document.addEventListener('click', this.handleClickOutside);
    },
    methods: {
        handleScroll() {
            if (window.pageYOffset > 0) {
                if (this.topOfPage) this.topOfPage = false
            } else {
                if (!this.topOfPage) this.topOfPage = true
            }
        },

        toggleMenu(event) {
            event.stopPropagation();
            this.activeChange = !this.activeChange;
        },
        handleClickOutside(event) {
            const navMenu = document.querySelector('.respon');
            if (navMenu && this.activeChange) {
                this.activeChange = !this.activeChange
            }
        }
    },

}

</script>
<style scoped lang="scss">
.navbar-nav * {
    font-size: 16px !important;
}

.container {
    transition: 0.3s;
}

.onScroll {
    box-shadow: 0 0 20px #aaa;
    background-color: white;
    transition: 0.5s;
}

.navbar-nav .nav-link {
    color: black;
}

.navbar-nav .nav-link::before {
    display: block;
    content: "";
    position: relative;
    width: 0%;
    height: 2px;
    background-color: #00a19b;
    left: 0;
    top: 30px;
}

.navbar-nav .nav-link:hover {
    color: var(--fontM-color);
}

.navbar-nav .nav-link:hover::before {
    width: 100%;
    transition: 0.3s;
}

.router-link-exact-active {
    color: var(--fontM-color) !important;
}

.navbar-nav .nav-link.active {
    color: var(--fontM-color);

}

.navbar-toggler-icon {
    background-image: none;
    width: 35px;
    height: 5px;
    background-color: #00a19b;
    margin: 0 0;
    transition: 0.4s;
    border-radius: 4px;
}

.icon {
    text-align: -webkit-right;
    height: auto !important;
}

.navbar-toggler:focus {
    box-shadow: none !important;
}

.icon .bar1,
.bar3 {
    width: 35px;
    height: 5px;
    background-color: #00a19b;
    margin: 0 0;
    transition: 0.4s;
    border-radius: 4px;
}

.bar2 {
    right: 0;
    width: 25px;
    height: 5px;
    background-color: #00a19b;
    margin: 6px 0;
    transition: 0.4s;
    border-radius: 4px;
}

.icon:hover {
    .bar2 {
        width: 35px;
    }
}

.X .bar1 {
    transform: translate(0, 11px) rotate(-45deg);
}

.X .bar2 {
    opacity: 0;
}

.X .bar3 {
    transform: translate(0, -11px) rotate(45deg);
}

.navbar-brand img {
    height: 50px;
    width: 55px;
}



@media(max-width: 992.98px) {

    .respon {
        display: block !important;
        background-color: white;
        transform: translateY(0px);
        animation-name: menuTran;
        animation-duration: 0.8s;
        animation-timing-function: ease;
        padding-bottom: 20px;

        @keyframes menuTran {
            0% {
                transform: translateY(-20px);
            }

            100% {
                transform: translateY(0px);
            }
        }

        .navbar-nav .nav-link::before {
            background-color: hsl(0, 0%, 4%);
            opacity: .3;
            height: 28px !important;
            top: 28px !important;
        }

        .navbar-nav .nav-link:hover::before {
            width: 100%;
            transition: 0.3s;
        }
    }
}

@media (max-width: 576.98px) {
    .respon {
        .navbar-nav .nav-link:hover::before {
            width: 0%;
        }
    }
}
</style>
